.ContactListItem {
  display: flex;
  align-items: center;
  padding: 5px;

  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}

.ContactListText {
  flex: 0 0 75%;
  margin-top: 0;
  margin-bottom: 0;

  border-radius: 3px;
}

.ContactListButton {
  margin-right: 4px;
  margin-left: 4px;
}
